import {getBalance, getBill} from '../../api/api.js';
	export default {
		data() {
			return {
				assetInfo: '',
				listInfo: '',
			}
		},
		methods: {
			getBalanceData() {
				getBalance().then(data => {
					this.assetInfo = data;
				});
			},
			getBillData() {
				getBill().then(data => {
					this.listInfo = data;
					// console.log(this.listInfo);
				});
			}
		},
		onLoad() {
			this.getBalanceData();
			this.getBillData();
		}
	}
